.header__links {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 15px 15px 0;
}

@media screen and (max-width: 812px) and (max-height: 425px) {
  .header__links {
    visibility: hidden;
    margin: 48px 13px 0;
    opacity: 0;
    transition: all 0.66s;
    font-size: 12px;
  }
}

@media screen and (max-width: 635px) {
  .header__links {
    visibility: hidden;
    margin: 48px 13px 0;
    opacity: 0;
    transition: all 0.66s;
  }
}
