.icons {
  outline: none;
  cursor: pointer;
  transition: all 0.66s;
  background: none;
  border: none;
  flex-shrink: 0;
  border: none;
  padding: 0;
}

.icons__play {
  width: 16px;
  height: 20px;
}

.icons__play_small {
  width: 8px;
  height: 10px;
}

.icons__pause {
  width: 13px;
  height: 16px;
}

.icons__toggle {
  width: 24px;
  height: 24px;
}

.icons__close {
  width: 24px;
  height: 24px;
}


